@font-face {
  font-family: 'Old Sans Black'; /* is (c) 2003 Manfred Klein, listed as "free for personal use" on https://fontzone.net/font-details/old-sans-black */
  src: url('fonts/OldSansBlack.woff2') format('woff2'),
       url('fonts/OldSansBlack.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}

body {
  font-family: system-ui;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #333;
  color: #eee;
  margin: 0;
}

a {
  &:link,
  &:visited {
    color: #cf54f4;
    text-decoration: none;
  }

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.four_by_three {
  background: url(./images/is-this-a-pigeon.jpg) no-repeat 100% 100%;
  background-size: cover;
  font-size: 12px;
  width: 640px;
  width: 133vh;
  max-width: 100%;
  height: 480px;
  height: 75vw;
  max-height: 100%;

  @media (min-width: 321px) and (min-height: 241px) {
    font-size: 14px;
  }

  @media (min-width: 641px) and (min-height: 481px) {
    font-size: 16px;
  }

  @media (min-width: 801px) and (min-height: 601px) {
    font-size: 18px;
  }

  @media (min-width: 961px) and (min-height: 720px) {
    font-size: 20px;
  }

  @media (min-width: 1025px) and (min-height: 769px) {
    font-size: 24px;
  }

  & * {
    text-shadow:
      0 0 1px #eee,
      0 -.09em 1px #333,
      -.04em -.08em 1px #333,
      -.08em -.08em 1px #333,
      -.08em -.04em 1px #333,
      -.09em 0 1px #333,
      -.08em .04em 1px #333,
      -.08em .08em 1px #333,
      -.04em .08em 1px #333,
      0 .09em 1px #333,
      .04em .08em 1px #333,
      .08em .08em 1px #333,
      .08em .04em 1px #333,
      .09em 0 1px #333,
      .08em -.04em 1px #333,
      .08em -.08em 1px #333,
      .04em -.08em 1px #333;
    color: rgba(255,255,255,0.01);
    transition: color 100ms ease-in-out;

    &:focus {
      color: #eee !important;
    }
  }
}

#is_this_a_github_ip_form {
  font-family: 'Old Sans Black', system-ui;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: space-between;
  margin: 0;
  padding: 4%;
  max-height: 100vh;
}

#ip_wrapper {
  display: flex;
  flex: 1 0 3em;
  order: -1;
  align-self: flex-end;
  flex-direction: column;
  justify-content: center;
}

#ip {
  border: none;
  border-radius: 1em;
  color: inherit;
  background: none;
  font: inherit;
  font-size: 2em;
  line-height: inherit;
  text-align: center;
  margin: 0;
  max-width: 100%;
  appearance: none;
  -webkit-appearance: none;
}

#result {
  margin: 0;
  font-size: 1.25em;
}

#footer {
  margin: 0 1em;
}
